import CreateMuiTheme from "@material-ui/core/styles/createMuiTheme";
//Main Theme
const Dark_Theme = CreateMuiTheme({
  palette: {
    type: "dark",
    primary: {
      main: "rgb(85,85,85)",
      dark: "rgb(166,166,166)" //Biss Gray
    },
    success: {
      main : "#bac778",
    },
    action: {
      selected: "#009c82"
    }
  },
  overrides: {
    MuiPaper: {
      root: {
        backgroundColor: "rgb(166,166,166)"
      }
    },
    MuiAppBar: {
      root: {
        backgroundColor: "rgb(85,85,85)"
      }
    }
  }
});

const Light_Theme = CreateMuiTheme({
  palette: {
    type: "light",
    text: {
      primary : "rgb(255,255,255)", //white text
      secondary : "rgb(100,100,100)" //dark text
    },
    primary: {
      light : "#4CACC1", //Biss light blue
      main: "#008AA8", //Biss Blue
      dark: "#00242C", //Biss Dark Blue
      darkGray : "#525252",
      lightGray : "#A9A8A8",
      lighterGray : "rgb(200,200,200)",
      white : "#ffffff",
      black : "#000000",
      boxShadow: "4px 4px #A9A8A8",
    },
    secondary: {
      light : "rgb(255,255,255)",
      main : "rgb(255,255,255)",
      dark: "rgb(255,255,255)"
    },
    action: {
      selected: "rgb(166,166,166)"
    }
  },
  overrides: {
    MuiAppBar: {
      root: {
        backgroundColor: "#008AA8"
      }
    },
    // MuiPaper: {
    //   root: {
    //     backgroundColor: "#008AA8"
    //   }
    // }
  }
});

const THEMES = {
  dark: Dark_Theme,
  light: Light_Theme
};

export default THEMES;
