import React from "react";
import { BrowserRouter as Router } from "react-router-dom";
import AppMsgBar from "components/ApplicationMessages";

import { MuiThemeProvider as ThemeProvider } from "@material-ui/core/styles";

import { useSelector } from "react-redux";

//Master Page Components
import AppBar from "components/MasterPage/AppBar";
import FOOTER from "components/MasterPage/Footer";
import MAIN from "components/MasterPage/Main";
import LEFT_MENU from "components/MasterPage/LeftMenu";
import { SnackbarProvider } from "notistack";
//Theme
import theme from "assets/jss/theme";
import useStyles from "./style";
import { CssBaseline } from "@material-ui/core";

export default function MasterPage() {
  const classes = useStyles();
  const currentTheme = useSelector(state =>
    state.theme === "dark" ? theme.dark : theme.light
  );
  return (
    <ThemeProvider theme={currentTheme}>
      <SnackbarProvider maxSnack={5}>
        <AppMsgBar/>
        <Router>
          <div className={classes.root}>
            <CssBaseline />
            <AppBar />
            <LEFT_MENU keepMounted drawerOpen />
            <MAIN drawerOpen />
          </div>
        </Router>
      </SnackbarProvider>
      <FOOTER />
    </ThemeProvider>
  );
}
