import { useSelector,useDispatch } from "react-redux";
import { useSnackbar } from "notistack";
import {hideSnackbar} from "redux/actions/snackbar";

export default function AppMessageBar(props) {
  const dispatch = useDispatch();
  const snackbar = useSelector(state => state.snackbar);
  const translate =useSelector(state=>state.language.menu);
  const { enqueueSnackbar } = useSnackbar();
  
  if(snackbar.show){
    enqueueSnackbar(translate.get(snackbar.message), { variant: snackbar.messageType });
    dispatch(hideSnackbar());
  }
  return null;
}
