import { SHOW_SNACKBAR, HIDE_SNACKBAR } from "redux/actions";

export function showSnackbar(message, messageType) {
  return function(dispatch) {
    return dispatch({
      type: SHOW_SNACKBAR,
      message,
      messageType
    });
  };
}

export function hideSnackbar() {
  return function(dispatch) {
    return dispatch({
      type: HIDE_SNACKBAR
    });
  };
}
