import React from "react";
import clsx from "clsx";
import Overview from "views/OverviewPage";
import Loading from "views/Loading";
import { Switch, Route } from "react-router-dom";
import { useSelector, useDispatch } from "react-redux";
import useStyles from "./style";
import { showSnackbar } from "redux/actions/snackbar";

// const Login = React.lazy(() => import("views/LoginPage"));
// const Overview = React.lazy(() => import("views/OverviewPage"));
// const Logs = React.lazy(() => import("views/LogsPage"));
// const Users = React.lazy(() => import("views/UsersPage"));

export default function Main(props) {
  const error = useSelector(state => state.error);
  const dispatch = useDispatch();
  const classes = useStyles();

  if (error.show) {
    dispatch(showSnackbar(error.status, "error"));
  }

  return (
    <main
      className={clsx(classes.content, {
        [classes.contentShift]: props.drawerOpen
      })}
    >
      <div className={classes.main}>
        <React.Fragment>
          <Loading/>
          <Switch>
            <Route exact path="/overview" component={Overview} />
            <Route path="/" component={Overview} />
          </Switch>
        </React.Fragment>
      </div>
    </main>
  );
}
