import {SHOW_LOADING, HIDE_LOADING, PAGE_TRANSITION} from "redux/actions/index";

export function showLoading() {
    return function(dispatch) {
        dispatch({
            type : SHOW_LOADING
        })
    }
}

export function hideLoading() {
    return function(dispatch) {
        dispatch({
            type : HIDE_LOADING
        })
    }
}

export function pageTransition() {
    return function(dispatch) {
        dispatch({
            type : PAGE_TRANSITION
        })
    }
}