import React from "react";
import clsx from "clsx";
import useStyles from "./style";
//core
import { Drawer, Paper, Grid } from "@material-ui/core";

import List from "@material-ui/core/List";
import Typography from "@material-ui/core/Typography";
import ListItem from "@material-ui/core/ListItem";
import ListItemIcon from "@material-ui/core/ListItemIcon";
import DASHBOARD_ICON from "assets/img/LeftMenu/dashboard.svg";
import { useHistory } from "react-router-dom";

export default function LeftMenu(props) {
  const history = useHistory();
  const classes = useStyles();
  const activePage = history.location.pathname;
  
  const profile = "TESTER";
  return (
    <Drawer
      anchor="left"
      variant="permanent"
      className={clsx(classes.drawer, {
        [classes.drawerOpen]: props.drawerOpen,
        [classes.drawerClose]: !props.drawerOpen
      })}
      classes={{
        paper: clsx({
          [classes.drawerOpen]: props.drawerOpen,
          [classes.drawerClose]: !props.drawerOpen
        })
      }}
      open={props.drawerOpen}
    >
      <List className={classes.list}>
      <Grid
          container
          justify="center"
          alignItems="center"
          className={classes.profileBox}
        >
          <Grid item xs={12}>
            <ListItem key="user_image">
              <ListItemIcon>
                <Paper className={classes.profilePaper}>
                  <Typography className={classes.profileTypography}>
                    {profile}
                  </Typography>
                </Paper>
              </ListItemIcon>
            </ListItem>
          </Grid>
        </Grid>
        <ListItem
          button
          onClick={() => history.replace("/overview")}
          key="overview"
        >
          <ListItemIcon>
            <Paper
              className={clsx(classes.iconPaper, {
                [classes.iconPaperActive]: activePage === "/overview"
              })}
            >
              <Typography className={classes.iconTypography}>
                <img
                  src={DASHBOARD_ICON}
                  className={classes.imgSize}
                  alt={"Overview"}
                />
              </Typography>
            </Paper>
          </ListItemIcon>
        </ListItem>
      </List>
    </Drawer>
  );
}
