import React, { useEffect, useState, useRef } from "react";
import Grid from "@material-ui/core/Grid";
import Box from "@material-ui/core/Box";
import { Typography } from "@material-ui/core";
import DOOR_OPEN from "assets/img/OverviewPage/door_open.svg";
import DOOR_CLOSE from "assets/img/OverviewPage/door_close.svg";
import DOOR_DISCONNECT from "assets/img/OverviewPage/door_disconnect.svg";
import useStyle from "./style";
import DOOR_CLOSED_LOCKED from "assets/img/OverviewPage/door_close_locked.svg";
import appConfig from "config/index";
import { useSelector, useDispatch } from "react-redux";
import { showLoading, hideLoading } from "redux/actions/loading";
import { getDoorStatus } from "api";

const DOOR_STATUS = appConfig.DOOR_STATUS;
const selectImg = data => {
  switch (data) {
    case DOOR_STATUS.CLOSE_UNLOCK:
      return DOOR_CLOSE;
    case DOOR_STATUS.OPEN_UNLOCK:
      return DOOR_OPEN;
    case DOOR_STATUS.CLOSE_LOCK:
      return DOOR_CLOSED_LOCKED;
    default:
      return DOOR_DISCONNECT;
  }
};

const setCurrentDateTime = (dateSetter, timeSetter) => {
  let date = new Date(Date.now());
  dateSetter(date.toLocaleDateString());
  timeSetter(date.toLocaleTimeString());
};

export default function DoorStatusCard() {
  const classes = useStyle();
  const dispatch = useDispatch();
  const translate = useSelector(state => state.language.menu);
  const error = useSelector(state => state.error.status);
  const title = translate.get("OverviewPage.doorCardTitle");
  const [date, setDate] = useState(null);
  const [time, setTime] = useState(null);
  const [status, setStatus] = useState(-1);
  const statusLabel = translate.get(`OverviewPage.door${status}`);
  const timer = useRef(null);
  
  useEffect(() => {
    if(error) {
      clearInterval(timer.current)
    } else {
      timer.current = setInterval(() => {
        getDoorStatus().then(res => {
          if (!res) return;
          setStatus(res.data.data);
          setCurrentDateTime(setDate, setTime);
        });
      }, 1000);
    }
  }, [error]);

  useEffect(() => {
    dispatch(showLoading());
    getDoorStatus().then(res => {
      if (!res) return;
      setStatus(res.data.data);
      setCurrentDateTime(setDate, setTime);
      dispatch(hideLoading());
    });
    return () => {
      clearInterval(timer.current);
    };
  }, [dispatch]);

  return (
    <Box className={classes.box}>
      <Grid container direction="row">
        <Grid item container xs={9} direction="column">
          <Grid item className={classes.boxHeader}>
            {title}
          </Grid>
          <Grid item className={classes.doorStatusItem}>
            {statusLabel}
          </Grid>
          <Grid item container direction="column">
            <Grid item className={classes.fontChildProperty}>
              {date}
            </Grid>
            <Grid item className={classes.fontChildProperty}>
              {time}
            </Grid>
          </Grid>
        </Grid>
        <Grid item xs={3} container justify="flex-end">
          <Typography variant="h5" noWrap>
            {
              <img
                className={classes.imgSize}
                src={selectImg(status)}
                alt={"door-" + status}
              />
            }
          </Typography>
        </Grid>
      </Grid>
    </Box>
  );
}
